/**
 * 
 * 
 * 
 */
import React, { createContext, useContext, useState, ReactNode } from 'react';
import { 
  AttenderApplicationData, 
  ExpertiseArea, 
  AvailabilityTimeSlot,
  SocialMediaLinks,
  ExperienceSample,
  Reference,
  AdditionalDocument,
  IdentificationDocument,
  FormStatusType
} from '../types/attender/index';
import { submitAttenderApplication } from '../services/AttenderService';

// 
export const REQUIRED_STEPS = ['BasicInfo', 'Identification', 'Agreements'];
export const OPTIONAL_STEPS = ['Expertise', 'ExperienceSamples', 'Availability'];

// 
export type StepKey = 'BasicInfo' | 'Identification' | 'Agreements' | 'Expertise' | 'ExperienceSamples' | 'Availability';

// 
export const STEP_METADATA: Record<StepKey, { title: string; description: string }> = {
  BasicInfo: { title: '', description: '' },
  Expertise: { title: '', description: '' },
  ExperienceSamples: { title: '', description: '' },
  Availability: { title: '', description: '' },
  Identification: { title: '', description: '' },
  Agreements: { title: '', description: '' }
};

// 
interface AttenderApplicationContextType {
  formData: Partial<AttenderApplicationData>;
  currentStep: number;
  isSubmitting: boolean;
  errors: Record<string, string>;
  formStatus: FormStatusType;
  maxSteps: number;
  
  // 
  setFormStatus: (status: FormStatusType) => void;
  
  // 
  updateFormData: (data: Partial<AttenderApplicationData>) => void;
  
  // 
  addExpertise: (expertise: ExpertiseArea) => void;
  updateExpertise: (index: number, expertise: ExpertiseArea) => void;
  removeExpertise: (index: number) => void;
  
  // 
  updateAvailabilityTimes: (availableTimes: AvailabilityTimeSlot[]) => void;
  
  // 
  addExperienceSample: (sample: ExperienceSample) => void;
  updateExperienceSample: (index: number, sample: ExperienceSample) => void;
  removeExperienceSample: (index: number) => void;
  
  // SNS
  updateSocialMediaLinks: (links: Partial<SocialMediaLinks>) => void;
  
  // 
  addReference: (reference: Reference) => void;
  updateReference: (index: number, reference: Reference) => void;
  removeReference: (index: number) => void;
  
  // 
  addDocument: (document: AdditionalDocument) => void;
  removeDocument: (index: number) => void;
  
  // 
  nextStep: () => void;
  
  // 
  prevStep: () => void;
  
  // 
  goToStep: (step: number) => void;
  
  // 
  submitForm: () => Promise<string>;
  
  // 
  setError: (field: string, message: string) => void;
  
  // 
  clearError: (field: string) => void;
  
  // 
  clearAllErrors: () => void;
  
  // 
  isStepCompleted: (step: number) => boolean;
}

// 
const initialFormData: Partial<AttenderApplicationData> = {
  specialties: [],
  languages: [],
  expertise: [],
  availableTimes: [],
  experienceSamples: [],
  isLocalResident: false, // 
  isMigrant: false, // 
  agreements: {
    termsOfService: false,
    privacyPolicy: false,
    codeOfConduct: false,
    backgroundCheck: false
  },
  formStatus: 'required' as const // 
};

// 
const AttenderApplicationContext = createContext<AttenderApplicationContextType | undefined>(undefined);

// 
export const AttenderApplicationProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [formData, setFormData] = useState<Partial<AttenderApplicationData>>(initialFormData);
  const [currentStep, setCurrentStep] = useState(1);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [errors, setErrors] = useState<Record<string, string>>({});
  const [formStatus, setFormStatus] = useState<FormStatusType>('required');
  
  // 
  const maxSteps = formStatus === 'required' 
    ? REQUIRED_STEPS.length 
    : REQUIRED_STEPS.length + OPTIONAL_STEPS.length;
  
  // 
  const updateFormData = (data: Partial<AttenderApplicationData>) => {
    setFormData(prev => ({ ...prev, ...data }));
  };
  
  // 
  const addExpertise = (expertise: ExpertiseArea) => {
    setFormData(prev => ({
      ...prev,
      expertise: [...(prev.expertise || []), expertise]
    }));
  };
  
  // 
  const updateExpertise = (index: number, expertise: ExpertiseArea) => {
    setFormData(prev => {
      const newExpertise = [...(prev.expertise || [])];
      newExpertise[index] = expertise;
      return { ...prev, expertise: newExpertise };
    });
  };
  
  // 
  const removeExpertise = (index: number) => {
    setFormData(prev => {
      const newExpertise = [...(prev.expertise || [])];
      newExpertise.splice(index, 1);
      return { ...prev, expertise: newExpertise };
    });
  };
  
  // 
  const updateAvailabilityTimes = (availableTimes: AvailabilityTimeSlot[]) => {
    setFormData(prev => ({ ...prev, availableTimes }));
  };
  
  // 
  const addExperienceSample = (sample: ExperienceSample) => {
    setFormData(prev => ({
      ...prev,
      experienceSamples: [...(prev.experienceSamples || []), sample]
    }));
  };
  
  // 
  const updateExperienceSample = (index: number, sample: ExperienceSample) => {
    setFormData(prev => {
      const newSamples = [...(prev.experienceSamples || [])];
      newSamples[index] = sample;
      return { ...prev, experienceSamples: newSamples };
    });
  };
  
  // 
  const removeExperienceSample = (index: number) => {
    setFormData(prev => {
      const newSamples = [...(prev.experienceSamples || [])];
      newSamples.splice(index, 1);
      return { ...prev, experienceSamples: newSamples };
    });
  };
  
  // SNS
  const updateSocialMediaLinks = (links: Partial<SocialMediaLinks>) => {
    setFormData(prev => ({
      ...prev,
      socialMediaLinks: { ...(prev.socialMediaLinks || {}), ...links }
    }));
  };
  
  // 
  const addReference = (reference: Reference) => {
    setFormData(prev => ({
      ...prev,
      references: [...(prev.references || []), reference]
    }));
  };
  
  // 
  const updateReference = (index: number, reference: Reference) => {
    setFormData(prev => {
      const newReferences = [...(prev.references || [])];
      newReferences[index] = reference;
      return { ...prev, references: newReferences };
    });
  };
  
  // 
  const removeReference = (index: number) => {
    setFormData(prev => {
      const newReferences = [...(prev.references || [])];
      newReferences.splice(index, 1);
      return { ...prev, references: newReferences };
    });
  };
  
  // 
  const addDocument = (document: AdditionalDocument) => {
    setFormData(prev => ({
      ...prev,
      additionalDocuments: [...(prev.additionalDocuments || []), document]
    }));
  };
  
  // 
  const removeDocument = (index: number) => {
    setFormData(prev => {
      const newDocuments = [...(prev.additionalDocuments || [])];
      newDocuments.splice(index, 1);
      return { ...prev, additionalDocuments: newDocuments };
    });
  };
  
  // 
  const nextStep = () => {
    if (currentStep < maxSteps) {
      setCurrentStep(currentStep + 1);
    }
  };
  
  // 
  const prevStep = () => {
    if (currentStep > 1) {
      setCurrentStep(currentStep - 1);
    }
  };
  
  // 
  const goToStep = (step: number) => {
    if (step >= 1 && step <= maxSteps) {
      setCurrentStep(step);
    }
  };
  
  // 
  const submitForm = async (): Promise<string> => {
    setIsSubmitting(true);
    clearAllErrors(); // 
    
    try {
      // 
      const validationErrors = validateForm(formData, formStatus);
      if (Object.keys(validationErrors).length > 0) {
        setErrors(validationErrors);
        setIsSubmitting(false);
        throw new Error('');
      }
      
      // 
      let completeFormData: AttenderApplicationData;
      
      if (formStatus === 'required') {
        // 
        completeFormData = {
          name: formData.name!,
          email: formData.email!,
          phoneNumber: formData.phoneNumber!,
          location: formData.location!,
          biography: formData.biography!,
          isLocalResident: formData.isLocalResident === true,
          isMigrant: formData.isMigrant === true,
          // 
        identificationDocument: formData.identificationDocument,
          agreements: formData.agreements!,
          // 
          specialties: [],
          languages: [],
          expertise: [],
          availableTimes: [],
          experienceSamples: [],
          // 
          formStatus: 'required' as const
        };
        
        // 
        if (formData.specialties) completeFormData.specialties = formData.specialties;
        if (formData.languages) completeFormData.languages = formData.languages;
        if (formData.yearsMoved) completeFormData.yearsMoved = formData.yearsMoved;
        if (formData.previousLocation) completeFormData.previousLocation = formData.previousLocation;
        
      } else {
        // 
        completeFormData = {
          ...formData as Partial<AttenderApplicationData>,
          // 
          name: formData.name!,
          email: formData.email!,
          phoneNumber: formData.phoneNumber!,
          location: formData.location!,
          biography: formData.biography!,
          specialties: formData.specialties || [],
          languages: formData.languages || [],
          isLocalResident: formData.isLocalResident === true,
          isMigrant: formData.isMigrant === true,
          expertise: formData.expertise || [],
          experienceSamples: formData.experienceSamples || [],
          availableTimes: formData.availableTimes || [],
          // 
          identificationDocument: formData.identificationDocument,
          agreements: formData.agreements!,
          // 
          formStatus: 'completed' as const
        };
      }
      
      console.info('...', formStatus === 'required' ? '()' : '()');
      
      // API
      try {
        verifyIdentificationDocument(formData.identificationDocument);
        verifyAgreements(formData.agreements);
        
        // 
        if (formStatus === 'completed' && formData.experienceSamples) {
          verifyExperienceSamples(formData.experienceSamples);
        }
      } catch (validationError) {
        setError('formValidation', validationError instanceof Error ? validationError.message : '');
        setIsSubmitting(false);
        throw validationError;
      }
      
      // API
      const applicationId = await submitAttenderApplication(completeFormData);
      console.info(`ID: ${applicationId}`);
      setIsSubmitting(false);
      return applicationId;
    } catch (error) {
      console.error(':', error);
      setIsSubmitting(false);
      
      // 
      const errorMessage = error instanceof Error
        ? error.message
        : '';
      
      // API
      if (errorMessage.includes('NETWORK_ERROR') || errorMessage.includes('TIMEOUT')) {
        setError('apiConnection', '');
      } else {
        setError('submission', errorMessage);
      }
      
      throw error;
    }
  };
  
  // 
  const setError = (field: string, message: string) => {
    setErrors(prev => ({ ...prev, [field]: message }));
  };
  
  // 
  const clearError = (field: string) => {
    setErrors(prev => {
      const newErrors = { ...prev };
      delete newErrors[field];
      return newErrors;
    });
  };
  
  // 
  const clearAllErrors = () => {
    setErrors({});
  };
  
  // 
  const isStepCompleted = (step: number): boolean => {
  // 
  const stepIndex = step - 1;
  
  // 
  const stepKeys = formStatus === 'required' 
  ? REQUIRED_STEPS 
  : [...REQUIRED_STEPS, ...OPTIONAL_STEPS];
  
  if (stepIndex < 0 || stepIndex >= stepKeys.length) {
  return false;
  }
  
  const stepKey = stepKeys[stepIndex] as StepKey;
  
  switch (stepKey) {
  case 'BasicInfo': // 
  return !!(    
  formData.name && 
  formData.email && 
  formData.phoneNumber && 
  formData.location && 
  formData.biography && 
  formData.isLocalResident !== undefined
  );
  case 'Identification': //  - 
  return true;
  case 'Agreements': // 
  return !!(    
      formData.agreements &&
    formData.agreements.termsOfService && 
  formData.agreements.privacyPolicy && 
  formData.agreements.codeOfConduct && 
  formData.agreements.backgroundCheck
  );
  case 'Expertise': // 
  return !!(    
      formData.specialties && 
    formData.specialties.length > 0 && 
  formData.languages && 
  formData.languages.length > 0 && 
  formData.expertise && 
  formData.expertise.length > 0
  );
  case 'ExperienceSamples': // 
  return !!(    
      formData.experienceSamples && 
    formData.experienceSamples.length > 0
  );
  case 'Availability': // 
  return !!(    
      formData.availableTimes && 
    formData.availableTimes.length > 0
  );
  default:
  return false;
  }
  };
  
  // 
  const verifyIdentificationDocument = (idDoc?: IdentificationDocument): void => {
    if (!idDoc) {
      throw new Error('');
    }
    
    // 
    if (idDoc.expirationDate) {
      const expirationDate = new Date(idDoc.expirationDate);
      const today = new Date();
      
      if (isNaN(expirationDate.getTime())) {
        throw new Error('');
      }
      
      if (expirationDate < today) {
        throw new Error('');
      }
    }
    
    // 
    if (!idDoc.frontImageUrl) {
      throw new Error('');
    }
    
    // URL
    if (!isValidImageUrl(idDoc.frontImageUrl)) {
      throw new Error('URL');
    }
    
    if (idDoc.backImageUrl && !isValidImageUrl(idDoc.backImageUrl)) {
      throw new Error('URL');
    }
  };
  
  // URL
  const isValidImageUrl = (url: string): boolean => {
    // URL
    return (
      url.startsWith('http://') ||
      url.startsWith('https://') ||
      url.startsWith('/uploads/') ||
      url.startsWith('data:image/')
    );
  };
  
  // 
  const verifyExperienceSamples = (samples: ExperienceSample[]): void => {
    if (samples.length === 0) {
      throw new Error('1');
    }
    
    samples.forEach((sample, index) => {
      // 
      if (!sample.title) throw new Error(`${index + 1}: `);
      if (!sample.description) throw new Error(`${index + 1}: `);
      if (sample.description.length < 50) {
        throw new Error(`${index + 1}: 50`);
      }
      
      // 
      if (sample.pricePerPerson < 0) {
        throw new Error(`${index + 1}: 0`);
      }
      
      // 
      if (!sample.estimatedDuration || sample.estimatedDuration <= 0) {
        throw new Error(`${index + 1}: `);
      }
      
      // sampleImagesimageUrls
      if (sample.imageUrls && sample.imageUrls.length > 0) {
        sample.imageUrls.forEach((imageUrl: string, imgIndex: number) => {
          if (!isValidImageUrl(imageUrl)) {
            throw new Error(`${index + 1}: ${imgIndex + 1}URL`);
          }
        });
      }
    });
  };
  
  // 
  const verifyAgreements = (agreements?: { termsOfService: boolean; privacyPolicy: boolean; codeOfConduct: boolean; backgroundCheck: boolean }): void => {
    if (!agreements) {
      throw new Error('');
    }
    
    if (!agreements.termsOfService) {
      throw new Error('');
    }
    
    if (!agreements.privacyPolicy) {
      throw new Error('');
    }
    
    if (!agreements.codeOfConduct) {
      throw new Error('');
    }
    
    if (!agreements.backgroundCheck) {
      throw new Error('');
    }
  };
  
  // 
  const validateForm = (data: Partial<AttenderApplicationData>, status: FormStatusType): Record<string, string> => {
    const validationErrors: Record<string, string> = {};
    
    // 1: 
    if (!data.name) validationErrors.name = '';
    if (!data.email) validationErrors.email = '';
    else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(data.email)) {
      validationErrors.email = '';
    }
    if (!data.phoneNumber) validationErrors.phoneNumber = '';
    if (!data.location?.country) validationErrors['location.country'] = '';
    if (!data.location?.region) validationErrors['location.region'] = '';
    if (!data.location?.city) validationErrors['location.city'] = '';
    if (!data.biography) validationErrors.biography = '';
    else if (data.biography.length < 50) {
      validationErrors.biography = '50';
    }
    if (data.isLocalResident === undefined) validationErrors.isLocalResident = '';
    
    // 
    if (data.isMigrant && !data.previousLocation) {
      validationErrors.previousLocation = '';
    }
    if (data.isMigrant && (!data.yearsMoved || data.yearsMoved <= 0)) {
      validationErrors.yearsMoved = '';
    }
    
    // 5: 
    if (!data.identificationDocument) {
      validationErrors.identificationDocument = '';
    } else {
      if (!data.identificationDocument.type) {
        validationErrors['identificationDocument.type'] = '';
      }
      if (!data.identificationDocument.number) {
        validationErrors['identificationDocument.number'] = '';
      }
      if (!data.identificationDocument.expirationDate) {
        validationErrors['identificationDocument.expirationDate'] = '';
      }
      if (!data.identificationDocument.frontImageUrl) {
        validationErrors['identificationDocument.frontImageUrl'] = '';
      }
    }
    
    // 6: 
    if (!data.agreements) {
      validationErrors.agreements = '';
    } else {
      if (!data.agreements.termsOfService) {
        validationErrors['agreements.termsOfService'] = '';
      }
      if (!data.agreements.privacyPolicy) {
        validationErrors['agreements.privacyPolicy'] = '';
      }
      if (!data.agreements.codeOfConduct) {
        validationErrors['agreements.codeOfConduct'] = '';
      }
      if (!data.agreements.backgroundCheck) {
        validationErrors['agreements.backgroundCheck'] = '';
      }
    }
    
    // 
    if (status !== 'required') {
      // 2: 
      if (!data.specialties || data.specialties.length === 0) {
        validationErrors.specialties = '1';
      }
      if (!data.languages || data.languages.length === 0) {
        validationErrors.languages = '1';
      }
      if (!data.expertise || data.expertise.length === 0) {
        validationErrors.expertise = '1';
      } else {
        data.expertise.forEach((expertise, index) => {
          if (!expertise.category) {
            validationErrors[`expertise[${index}].category`] = '';
          }
          if (!expertise.subcategories || expertise.subcategories.length === 0) {
            validationErrors[`expertise[${index}].subcategories`] = '1';
          }
          if (expertise.yearsOfExperience === undefined || expertise.yearsOfExperience < 0) {
            validationErrors[`expertise[${index}].yearsOfExperience`] = '';
          }
          if (!expertise.description) {
            validationErrors[`expertise[${index}].description`] = '';
          }
        });
      }
      
      // 3: 
      if (!data.experienceSamples || data.experienceSamples.length === 0) {
        validationErrors.experienceSamples = '1';
      } else {
        data.experienceSamples.forEach((sample, index) => {
          if (!sample.title) {
            validationErrors[`experienceSamples[${index}].title`] = '';
          }
          if (!sample.description) {
            validationErrors[`experienceSamples[${index}].description`] = '';
          }
          if (!sample.category) {
            validationErrors[`experienceSamples[${index}].category`] = '';
          }
          if (!sample.estimatedDuration || sample.estimatedDuration <= 0) {
            validationErrors[`experienceSamples[${index}].estimatedDuration`] = '';
          }
          if (!sample.maxParticipants || sample.maxParticipants <= 0) {
            validationErrors[`experienceSamples[${index}].maxParticipants`] = '';
          }
          if (sample.pricePerPerson === undefined || sample.pricePerPerson < 0) {
            validationErrors[`experienceSamples[${index}].pricePerPerson`] = '';
          }
        });
      }
      
      // 4: 
      if (!data.availableTimes || data.availableTimes.length === 0) {
        validationErrors.availableTimes = '1';
      } else {
        const hasAvailable = data.availableTimes.some(time => time.isAvailable);
        if (!hasAvailable) {
          validationErrors.availableTimes = '1';
        }
      }
    }
    
    return validationErrors;
  };
  
  // 
  const contextValue: AttenderApplicationContextType = {
    formData,
    currentStep,
    isSubmitting,
    errors,
    formStatus,
    maxSteps,
    setFormStatus,
    updateFormData,
    addExpertise,
    updateExpertise,
    removeExpertise,
    updateAvailabilityTimes,
    addExperienceSample,
    updateExperienceSample,
    removeExperienceSample,
    updateSocialMediaLinks,
    addReference,
    updateReference,
    removeReference,
    addDocument,
    removeDocument,
    nextStep,
    prevStep,
    goToStep,
    submitForm,
    setError,
    clearError,
    clearAllErrors,
    isStepCompleted
  };
  
  return (
    <AttenderApplicationContext.Provider value={contextValue}>
      {children}
    </AttenderApplicationContext.Provider>
  );
};

// 
export const useAttenderApplication = (): AttenderApplicationContextType => {
  const context = useContext(AttenderApplicationContext);
  if (context === undefined) {
    throw new Error('useAttenderApplication must be used within a AttenderApplicationProvider');
  }
  return context;
};

export default AttenderApplicationContext;
