import React from 'react';

interface Experience {
  id: string;
  title: string;
}

// FiltersPropsFilterState
export interface FilterState {
  ratingFilter: number | null;
  photoFilter: boolean | null;
  dateRange: {
    from: string | null;
    to: string | null;
  };
  searchTerm: string;
  sortBy: string;
  // 
  rating?: number | null;
  experience?: string | null;
  period?: 'all' | 'month' | 'week' | 'day';
  withPhotos?: boolean;
  withReplies?: boolean;
  noReplies?: boolean;
}

interface FiltersProps {
  filters: FilterState;
  experiences: Experience[];
  onFilterChange: (newFilters: Partial<FilterState>) => void;
  onResetFilters: () => void;
  // ReviewsList.tsxprops
  totalReviews?: number;
  reviewCounts?: number[];
  photoReviewsCount?: number;
  recentReviewsCount?: number;
}

const ReviewFilters: React.FC<FiltersProps> = ({
  filters,
  experiences,
  onFilterChange,
  onResetFilters
}) => {
  // 
  const uniqueExperiences = experiences.filter(
    (experience, index, self) =>
      index === self.findIndex((e) => e.id === experience.id)
  );

  return (
    <div className="bg-gray-50 p-4 rounded-lg mb-4">
      <div className="flex items-center justify-between mb-4">
        <h4 className="text-sm font-medium text-gray-700"></h4>
        <button
          type="button"
          onClick={onResetFilters}
          className="text-sm text-blue-600 hover:text-blue-800"
        >
          
        </button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        {/*  */}
        <div>
          <label
            htmlFor="rating-filter"
            className="block text-sm font-medium text-gray-700 mb-1"
          >
            
          </label>
          <select
            id="rating-filter"
            className="block w-full p-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
            value={filters.rating === null ? '' : filters.rating}
            onChange={(e) =>
              onFilterChange({
                rating: e.target.value === '' ? null : Number(e.target.value)
              })
            }
          >
            <option value=""></option>
            <option value="5">5</option>
            <option value="4">4</option>
            <option value="3">3</option>
            <option value="2">2</option>
            <option value="1">1</option>
          </select>
        </div>

        {/*  */}
        <div>
          <label
            htmlFor="experience-filter"
            className="block text-sm font-medium text-gray-700 mb-1"
          >
            
          </label>
          <select
            id="experience-filter"
            className="block w-full p-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
            value={filters.experience === null ? '' : filters.experience}
            onChange={(e) =>
              onFilterChange({
                experience: e.target.value === '' ? null : e.target.value
              })
            }
          >
            <option value=""></option>
            {uniqueExperiences.map((exp) => (
              <option key={exp.id} value={exp.id}>
                {exp.title}
              </option>
            ))}
          </select>
        </div>

        {/*  */}
        <div>
          <label
            htmlFor="period-filter"
            className="block text-sm font-medium text-gray-700 mb-1"
          >
            
          </label>
          <select
            id="period-filter"
            className="block w-full p-2 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
            value={filters.period}
            onChange={(e) =>
              onFilterChange({
                period: e.target.value as 'all' | 'month' | 'week' | 'day'
              })
            }
          >
            <option value="all"></option>
            <option value="month">1</option>
            <option value="week">1</option>
            <option value="day">24</option>
          </select>
        </div>
      </div>

      <div className="mt-4 flex flex-wrap gap-4">
        {/*  */}
        <label className="inline-flex items-center">
          <input
            type="checkbox"
            className="form-checkbox h-4 w-4 text-blue-600 rounded"
            checked={filters.withPhotos}
            onChange={(e) =>
              onFilterChange({ withPhotos: e.target.checked })
            }
          />
          <span className="ml-2 text-sm text-gray-700"></span>
        </label>

        {/*  */}
        <label className="inline-flex items-center">
          <input
            type="checkbox"
            className="form-checkbox h-4 w-4 text-blue-600 rounded"
            checked={filters.withReplies}
            onChange={(e) =>
              onFilterChange({
                withReplies: e.target.checked,
                // 
                noReplies: e.target.checked ? false : filters.noReplies
              })
            }
          />
          <span className="ml-2 text-sm text-gray-700"></span>
        </label>

        {/*  */}
        <label className="inline-flex items-center">
          <input
            type="checkbox"
            className="form-checkbox h-4 w-4 text-blue-600 rounded"
            checked={filters.noReplies}
            onChange={(e) =>
              onFilterChange({
                noReplies: e.target.checked,
                // 
                withReplies: e.target.checked ? false : filters.withReplies
              })
            }
          />
          <span className="ml-2 text-sm text-gray-700"></span>
        </label>
      </div>
    </div>
  );
};

export default ReviewFilters;
