/**
 * 
 * @param file 
 * @param options 
 * @returns Promise<File> 
 */
export const compressImage = (
  file: File,
  options: {
    quality?: number; //  (0-1)
    maxWidth?: number; // 
    maxHeight?: number; // 
    maxSize?: number; // 
  } = {}
): Promise<File> => {
  const {
    quality = 0.8,
    maxWidth = 1920,
    maxHeight = 1080,
    maxSize = 5 * 1024 * 1024, // 5MB
  } = options;

  // 
  if (file.size <= maxSize) {
    console.log('Image is already small enough, skipping compression');
    return Promise.resolve(file);
  }

  return new Promise((resolve, reject) => {
    // 
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = (event) => {
      const img = new Image();
      img.src = event.target?.result as string;
      
      img.onload = () => {
        // 
        let width = img.width;
        let height = img.height;
        
        if (width > maxWidth) {
          const ratio = maxWidth / width;
          width = maxWidth;
          height = height * ratio;
        }
        
        if (height > maxHeight) {
          const ratio = maxHeight / height;
          height = maxHeight;
          width = width * ratio;
        }
        
        // Canvas 
        const canvas = document.createElement('canvas');
        canvas.width = width;
        canvas.height = height;
        
        const ctx = canvas.getContext('2d');
        if (!ctx) {
          reject(new Error('Canvas context could not be created'));
          return;
        }
        
        ctx.drawImage(img, 0, 0, width, height);
        
        // Blob 
        canvas.toBlob(
          (blob) => {
            if (!blob) {
              reject(new Error('Canvas could not create blob'));
              return;
            }
            
            const compressedFile = new File([blob], file.name, {
              type: file.type,
              lastModified: new Date().getTime(),
            });
            
            console.log(`: ${file.size}  ${compressedFile.size} bytes`);
            resolve(compressedFile);
          },
          file.type,
          quality
        );
      };
      
      img.onerror = () => {
        reject(new Error('Image loading failed'));
      };
    };
    
    reader.onerror = () => {
      reject(new Error('File reading failed'));
    };
  });
};

/**
 * Exif
 * @param file 
 * @returns Promise<File> Exif
 */
export const removeExifData = (file: File): Promise<File> => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const img = new Image();
        img.onload = () => {
          const canvas = document.createElement('canvas');
          canvas.width = img.width;
          canvas.height = img.height;
          
          const ctx = canvas.getContext('2d');
          if (!ctx) {
            reject(new Error('Canvas context could not be created'));
            return;
          }
          
          ctx.drawImage(img, 0, 0);
          
          canvas.toBlob(
            (blob) => {
              if (!blob) {
                reject(new Error('Canvas could not create blob'));
                return;
              }
              
              const newFile = new File([blob], file.name, {
                type: file.type,
                lastModified: new Date().getTime(),
              });
              
              console.log(`EXIF : ${file.name}`);
              resolve(newFile);
            },
            file.type,
            0.95 // 
          );
        };
        
        img.onerror = () => {
          reject(new Error('Image loading failed'));
        };
        
        img.src = e.target?.result as string;
      } catch (error) {
        reject(error);
      }
    };
    
    reader.onerror = () => {
      reject(new Error('FileReader failed'));
    };
    
    reader.readAsDataURL(file);
  });
};

/**
 * EXIF
 * @param file 
 * @param options 
 * @returns Promise<File> 
 */
export const processImage = async (
  file: File,
  options: {
    quality?: number;
    maxWidth?: number;
    maxHeight?: number;
    maxSize?: number;
    removeExif?: boolean;
  } = {}
): Promise<File> => {
  const { removeExif = true } = options;
  
  try {
    // 
    let processedFile = await compressImage(file, options);
    
    // EXIF
    if (removeExif) {
      processedFile = await removeExifData(processedFile);
    }
    
    return processedFile;
  } catch (error) {
    console.error(':', error);
    // 
    return file;
  }
};

/**
 * 
 * @param fileType MIME
 * @returns boolean true
 */
export const isSupportedImageType = (fileType: string): boolean => {
  const supportedTypes = [
    'image/jpeg',
    'image/png',
    'image/gif',
    'image/webp',
    'image/heic',
    'image/heif'
  ];
  
  return supportedTypes.includes(fileType);
};

/**
 * HEICJPEG
 * @param file HEIC
 * @returns Promise<File> JPEG
 */
export const convertHeicToJpeg = async (file: File): Promise<File> => {
  try {
    // HEICJPEG
    //  heic2any 
    // 
    await new Promise(resolve => setTimeout(resolve, 500));
    
    // 
    console.log('HEIC  JPEG ');
    
    // 
    const newFileName = file.name.replace(/\.heic$/i, '.jpg');
    
    // 
    return new File([file], newFileName, {
      type: 'image/jpeg',
      lastModified: new Date().getTime(),
    });
  } catch (error) {
    console.error('HEIC:', error);
    return file;
  }
};
