import api, { logApiRequest, logApiResponse } from '../../utils/apiClient';
import { ENDPOINTS } from '../../config/api';
import { isDevelopment } from '../../config/env';
import { processImage, isSupportedImageType, convertHeicToJpeg } from '../../utils/imageUtils';

// 
export type UploadType = 'image' | 'document' | 'profile' | 'experience' | 'identification';

// 
export type UploadStatus = 'pending' | 'processing' | 'uploading' | 'success' | 'error';

// 
export interface UploadProgress {
  status: UploadStatus;
  progress: number; // 0-100
  file?: File;
  url?: string;
  error?: string;
}

// 
export type ProgressCallback = (progress: UploadProgress) => void;

/**
 * 
 * 
 * @param file 
 * @param type 
 * @param onProgress 
 * @param additionalData 
 * @returns URL
 */
export async function uploadImage(
  file: File,
  type: UploadType = 'image',
  onProgress?: ProgressCallback,
  additionalData?: Record<string, string>
): Promise<string> {
  try {
    // 
    if (onProgress) {
      onProgress({
        status: 'processing',
        progress: 0,
        file
      });
    }

    let processedFile = file;
    const mimeType = file.type.toLowerCase();

    // HEICJPEG
    if (mimeType === 'image/heic' || mimeType === 'image/heif') {
      if (onProgress) {
        onProgress({
          status: 'processing',
          progress: 10,
          file
        });
      }
      processedFile = await convertHeicToJpeg(file);
    }

    // 
    if (!isSupportedImageType(processedFile.type)) {
      throw new Error(`: ${processedFile.type}`);
    }

    // EXIF
    if (onProgress) {
      onProgress({
        status: 'processing',
        progress: 30,
        file: processedFile
      });
    }
    
    processedFile = await processImage(processedFile, {
      quality: 0.85,
      maxWidth: 2000,
      maxHeight: 2000,
      maxSize: 5 * 1024 * 1024, // 5MB
      removeExif: true
    });

    // 
    if (onProgress) {
      onProgress({
        status: 'uploading',
        progress: 50,
        file: processedFile
      });
    }

    // 
    let endpoint;
    switch (type) {
      case 'profile':
        endpoint = ENDPOINTS.UPLOAD.PROFILE_PHOTO;
        break;
      case 'experience':
        endpoint = ENDPOINTS.UPLOAD.EXPERIENCE_PHOTO;
        break;
      case 'identification':
        endpoint = ENDPOINTS.UPLOAD.DOCUMENT;
        break;
      default:
        endpoint = ENDPOINTS.UPLOAD.IMAGE;
    }

    // 
    if (isDevelopment()) {
      // 
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      // URL
      const timestamp = new Date().getTime();
      const sanitizedFileName = processedFile.name.replace(/[^a-z0-9.]/gi, '_').toLowerCase();
      const mockUrl = `/uploads/${type}/${timestamp}_${sanitizedFileName}`;
      
      console.info(`: ${processedFile.name}URL: ${mockUrl}`);
      
      // 
      if (onProgress) {
        onProgress({
          status: 'success',
          progress: 100,
          file: processedFile,
          url: mockUrl
        });
      }
      
      return mockUrl;
    }

    // 
    logApiRequest('POST', endpoint, { filename: processedFile.name, fileSize: processedFile.size });

    // 
    const response = await api.uploadFile(
      endpoint,
      processedFile,
      'file',
      additionalData
    );

    // 
    logApiResponse('POST', endpoint, response);

    // 
    if (!response.success || !response.data?.url) {
      const errorMessage = response.error?.message || '';
      if (onProgress) {
        onProgress({
          status: 'error',
          progress: 0,
          file: processedFile,
          error: errorMessage
        });
      }
      throw new Error(errorMessage);
    }

    // 
    if (onProgress) {
      onProgress({
        status: 'success',
        progress: 100,
        file: processedFile,
        url: response.data.url
      });
    }

    return response.data.url;
  } catch (error) {
    console.error(':', error);
    
    // 
    if (onProgress) {
      onProgress({
        status: 'error',
        progress: 0,
        file,
        error: error instanceof Error ? error.message : ''
      });
    }
    
    throw error;
  }
}

/**
 * 
 * 
 * @param file 
 * @param onProgress 
 * @param additionalData 
 * @returns URL
 */
export async function uploadDocument(
  file: File,
  onProgress?: ProgressCallback,
  additionalData?: Record<string, string>
): Promise<string> {
  try {
    // 
    if (onProgress) {
      onProgress({
        status: 'uploading',
        progress: 0,
        file
      });
    }

    // 
    const endpoint = ENDPOINTS.UPLOAD.DOCUMENT;

    // 
    if (isDevelopment()) {
      // 
      await new Promise(resolve => setTimeout(resolve, 1200));
      
      // URL
      const timestamp = new Date().getTime();
      const sanitizedFileName = file.name.replace(/[^a-z0-9.]/gi, '_').toLowerCase();
      const mockUrl = `/uploads/documents/${timestamp}_${sanitizedFileName}`;
      
      console.info(`: ${file.name}URL: ${mockUrl}`);
      
      // 
      if (onProgress) {
        const steps = [25, 50, 75, 100];
        for (const progress of steps) {
          await new Promise(resolve => setTimeout(resolve, 300));
          onProgress({
            status: progress === 100 ? 'success' : 'uploading',
            progress,
            file,
            ...(progress === 100 ? { url: mockUrl } : {})
          });
        }
      }
      
      return mockUrl;
    }

    // 
    logApiRequest('POST', endpoint, { filename: file.name, fileSize: file.size });

    // 
    const response = await api.uploadFile(
      endpoint,
      file,
      'document',
      additionalData
    );

    // 
    logApiResponse('POST', endpoint, response);

    // 
    if (!response.success || !response.data?.url) {
      const errorMessage = response.error?.message || '';
      if (onProgress) {
        onProgress({
          status: 'error',
          progress: 0,
          file,
          error: errorMessage
        });
      }
      throw new Error(errorMessage);
    }

    // 
    if (onProgress) {
      onProgress({
        status: 'success',
        progress: 100,
        file,
        url: response.data.url
      });
    }

    return response.data.url;
  } catch (error) {
    console.error(':', error);
    
    // 
    if (onProgress) {
      onProgress({
        status: 'error',
        progress: 0,
        file,
        error: error instanceof Error ? error.message : ''
      });
    }
    
    throw error;
  }
}

/**
 * 
 * 
 * @param file 
 * @param identificationType 
 * @param onProgress 
 * @returns URL
 */
export async function uploadIdentificationDocument(
  file: File,
  identificationType: string,
  onProgress?: ProgressCallback
): Promise<string> {
  const additionalData = {
    documentType: identificationType,
    purpose: 'identity_verification'
  };

  // URL
  if (file.type.startsWith('image/')) {
    return uploadImage(file, 'identification', onProgress, additionalData);
  }
  
  // PDF
  return uploadDocument(file, onProgress, additionalData);
}

/**
 * 
 * 
 * @param file 
 * @param experienceTitle 
 * @param onProgress 
 * @returns URL
 */
export async function uploadExperienceImage(
  file: File,
  experienceTitle: string,
  onProgress?: ProgressCallback
): Promise<string> {
  const additionalData = {
    experienceTitle,
    caption: `${experienceTitle}`
  };
  
  return uploadImage(file, 'experience', onProgress, additionalData);
}

/**
 * 
 * 
 * @param file 
 * @param userId ID
 * @param onProgress 
 * @returns URL
 */
export async function uploadProfileImage(
  file: File,
  userId?: string,
  onProgress?: ProgressCallback
): Promise<string> {
  const additionalData = userId ? { userId } : undefined;
  return uploadImage(file, 'profile', onProgress, additionalData);
}

export default {
  uploadImage,
  uploadDocument,
  uploadIdentificationDocument,
  uploadExperienceImage,
  uploadProfileImage
};
