/**
 * 
 * 
 * 
 */
import React, { useState } from 'react';
import { useAttenderApplication } from '../../../../contexts/AttenderApplicationContext';
import { ExperienceSample } from '../../../../types/attender/index';

interface ExperienceSamplesStepProps {
  onNext: () => void;
  onBack: () => void;
}

const ExperienceSamplesStep: React.FC<ExperienceSamplesStepProps> = ({ onNext, onBack }) => {
  const { 
    formData, 
    addExperienceSample,
    updateExperienceSample,
    removeExperienceSample,
    errors, 
    clearError 
  } = useAttenderApplication();
  
  // 
  const experienceCategories = [
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    ''
  ];
  
  // 
  const [newSample, setNewSample] = useState<ExperienceSample>({
    title: '',
    description: '',
    category: '',
    estimatedDuration: 120, // 2
    maxParticipants: 4, // 4
    pricePerPerson: 5000, // 5000
    includesFood: false,
    includesTransportation: false,
    cancellationPolicy: 'moderate'
  } as ExperienceSample);
  
  // 
  const [editIndex, setEditIndex] = useState<number | null>(null);
  
  // 
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value, type } = e.target;
    
    // 
    if (type === 'checkbox') {
      const checked = (e.target as HTMLInputElement).checked;
      setNewSample(prev => ({
        ...prev,
        [name]: checked
      }));
    }
    // 
    else if (type === 'number') {
      setNewSample(prev => ({
        ...prev,
        [name]: parseFloat(value) || 0
      }));
    }
    // 
    else {
      setNewSample(prev => ({
        ...prev,
        [name]: value
      }));
    }
  };
  
  // 
  const handleAddSample = () => {
    if (
      newSample.title &&
      newSample.description &&
      newSample.category
    ) {
      if (editIndex !== null) {
        // 
        updateExperienceSample(editIndex, newSample);
        setEditIndex(null);
      } else {
        // 
        addExperienceSample(newSample);
      }
      
      // 
      setNewSample({
        title: '',
        description: '',
        category: '',
        estimatedDuration: 120,
        maxParticipants: 4,
        pricePerPerson: 5000,
        includesFood: false,
        includesTransportation: false,
        cancellationPolicy: 'moderate'
      } as ExperienceSample);
      
      // 
      clearError('experienceSamples');
    }
  };
  
  // 
  const handleEditSample = (index: number) => {
    const sample = formData.experienceSamples?.[index];
    if (sample) {
      setNewSample(sample);
      setEditIndex(index);
    }
  };
  
  // 
  const handleRemoveSample = (index: number) => {
    removeExperienceSample(index);
    
    // 
    if (editIndex === index) {
      setEditIndex(null);
      setNewSample({
        title: '',
        description: '',
        category: '',
        estimatedDuration: 120,
        maxParticipants: 4,
        pricePerPerson: 5000,
        includesFood: false,
        includesTransportation: false,
        cancellationPolicy: 'moderate'
      } as ExperienceSample);
    }
  };
  
  // 
  const handleCancelEdit = () => {
    setEditIndex(null);
    setNewSample({
      title: '',
      description: '',
      category: '',
      estimatedDuration: 120,
      maxParticipants: 4,
      pricePerPerson: 5000,
      includesFood: false,
      includesTransportation: false,
      cancellationPolicy: 'moderate'
    });
  };
  
  return (
    <div className="space-y-8">
      <h2 className="text-xl font-semibold mb-4"></h2>
      <p className="text-sm text-gray-500">
        1
        
      </p>
      
      {/*  */}
      {(formData.experienceSamples || []).length > 0 && (
        <div className="mb-6 space-y-4">
          <h3 className="text-lg font-medium text-gray-700"></h3>
          {(formData.experienceSamples || []).map((sample, index) => (
            <div key={index} className="bg-gray-50 p-4 rounded-md relative">
              <div className="absolute top-2 right-2 flex space-x-2">
                <button
                  type="button"
                  onClick={() => handleEditSample(index)}
                  className="text-blue-500 hover:text-blue-600"
                >
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                    <path d="M13.586 3.586a2 2 0 112.828 2.828l-.793.793-2.828-2.828.793-.793zM11.379 5.793L3 14.172V17h2.828l8.38-8.379-2.83-2.828z" />
                  </svg>
                </button>
                <button
                  type="button"
                  onClick={() => handleRemoveSample(index)}
                  className="text-red-500 hover:text-red-600"
                >
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                    <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                  </svg>
                </button>
              </div>
              
              <h4 className="font-semibold text-lg mb-1">{sample.title}</h4>
              <p className="text-sm text-blue-600 mb-2">{sample.category}</p>
              <p className="text-sm text-gray-700 mb-3">{sample.description}</p>
              
              <div className="grid grid-cols-2 gap-x-4 gap-y-2 text-sm">
                <div>
                  <span className="font-medium">:</span> {sample.estimatedDuration}
                </div>
                <div>
                  <span className="font-medium">:</span> {sample.maxParticipants}
                </div>
                <div>
                  <span className="font-medium">:</span> {sample.pricePerPerson.toLocaleString()}/
                </div>
                <div>
                  <span className="font-medium">:</span>{' '}
                  {sample.cancellationPolicy === 'flexible' ? '' : 
                   sample.cancellationPolicy === 'moderate' ? '' : ''}
                </div>
              </div>
              
              <div className="mt-3 flex flex-wrap gap-2">
                {sample.includesFood && (
                  <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                    
                  </span>
                )}
                {sample.includesTransportation && (
                  <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                    
                  </span>
                )}
                {sample.specialRequirements && (
                  <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
                    
                  </span>
                )}
              </div>
            </div>
          ))}
        </div>
      )}
      
      {/*  */}
      <div className="bg-gray-50 p-4 rounded-md">
        <h3 className="text-lg font-medium text-gray-700 mb-4">
          {editIndex !== null ? '' : ''}
        </h3>
        
        {/*  */}
        <div className="mb-4">
          <label htmlFor="title" className="block text-sm font-medium text-gray-700 mb-1">
             <span className="text-red-500">*</span>
          </label>
          <input
            type="text"
            id="title"
            name="title"
            value={newSample.title}
            onChange={handleInputChange}
            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            placeholder=""
          />
        </div>
        
        {/*  */}
        <div className="mb-4">
          <label htmlFor="category" className="block text-sm font-medium text-gray-700 mb-1">
             <span className="text-red-500">*</span>
          </label>
          <select
            id="category"
            name="category"
            value={newSample.category}
            onChange={handleInputChange}
            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
          >
            <option value=""></option>
            {experienceCategories.map(category => (
              <option key={category} value={category}>{category}</option>
            ))}
          </select>
        </div>
        
        {/*  */}
        <div className="mb-4">
          <label htmlFor="description" className="block text-sm font-medium text-gray-700 mb-1">
             <span className="text-red-500">*</span>
          </label>
          <textarea
            id="description"
            name="description"
            rows={4}
            value={newSample.description}
            onChange={handleInputChange}
            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            placeholder=""
          />
        </div>
        
        {/*  */}
        <div className="mb-4">
          <label htmlFor="estimatedDuration" className="block text-sm font-medium text-gray-700 mb-1">
             <span className="text-red-500">*</span>
          </label>
          <input
            type="number"
            id="estimatedDuration"
            name="estimatedDuration"
            min="30"
            step="30"
            value={newSample.estimatedDuration}
            onChange={handleInputChange}
            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
          />
          <p className="mt-1 text-sm text-gray-500">30</p>
        </div>
        
        {/*  */}
        <div className="mb-4">
          <label htmlFor="maxParticipants" className="block text-sm font-medium text-gray-700 mb-1">
             <span className="text-red-500">*</span>
          </label>
          <input
            type="number"
            id="maxParticipants"
            name="maxParticipants"
            min="1"
            value={newSample.maxParticipants}
            onChange={handleInputChange}
            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
          />
        </div>
        
        {/*  */}
        <div className="mb-4">
          <label htmlFor="pricePerPerson" className="block text-sm font-medium text-gray-700 mb-1">
            1 <span className="text-red-500">*</span>
          </label>
          <input
            type="number"
            id="pricePerPerson"
            name="pricePerPerson"
            min="0"
            step="500"
            value={newSample.pricePerPerson}
            onChange={handleInputChange}
            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
          />
        </div>
        
        {/*  */}
        <div className="mb-4">
          <fieldset>
            <legend className="text-sm font-medium text-gray-700 mb-2"></legend>
            <div className="space-y-2">
              <div className="flex items-center">
                <input
                  id="includesFood"
                  name="includesFood"
                  type="checkbox"
                  checked={newSample.includesFood}
                  onChange={handleInputChange}
                  className="h-4 w-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                />
                <label htmlFor="includesFood" className="ml-2 block text-sm text-gray-700">
                  
                </label>
              </div>
              <div className="flex items-center">
                <input
                  id="includesTransportation"
                  name="includesTransportation"
                  type="checkbox"
                  checked={newSample.includesTransportation}
                  onChange={handleInputChange}
                  className="h-4 w-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                />
                <label htmlFor="includesTransportation" className="ml-2 block text-sm text-gray-700">
                  
                </label>
              </div>
            </div>
          </fieldset>
        </div>
        
        {/*  */}
        <div className="mb-4">
          <label htmlFor="specialRequirements" className="block text-sm font-medium text-gray-700 mb-1">
            
          </label>
          <textarea
            id="specialRequirements"
            name="specialRequirements"
            rows={2}
            value={newSample.specialRequirements || ''}
            onChange={handleInputChange}
            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            placeholder=""
          />
        </div>
        
        {/*  */}
        <div className="mb-4">
          <label htmlFor="cancellationPolicy" className="block text-sm font-medium text-gray-700 mb-1">
             <span className="text-red-500">*</span>
          </label>
          <select
            id="cancellationPolicy"
            name="cancellationPolicy"
            value={newSample.cancellationPolicy}
            onChange={handleInputChange}
            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
          >
            <option value="flexible">24</option>
            <option value="moderate">3</option>
            <option value="strict">7</option>
          </select>
        </div>
        
        {/*  */}
        <div className="mt-6 flex justify-end space-x-3">
          {editIndex !== null && (
            <button
              type="button"
              onClick={handleCancelEdit}
              className="px-4 py-2 bg-gray-200 text-gray-700 rounded-md hover:bg-gray-300 focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2"
            >
              
            </button>
          )}
          <button
            type="button"
            onClick={handleAddSample}
            disabled={!newSample.title || !newSample.description || !newSample.category}
            className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:bg-blue-300 disabled:cursor-not-allowed"
          >
            {editIndex !== null ? '' : ''}
          </button>
        </div>
      </div>
      
      {errors.experienceSamples && (
        <p className="mt-2 text-sm text-red-500">{errors.experienceSamples}</p>
      )}
    </div>
  );
};

export default ExperienceSamplesStep;
